import React, { useState, useEffect, useCallback } from "react";
import styled from "styled-components";
import Header from "../components/Header";
import Sidebar from "../components/Sidebar";
import PostCard from "../components/PostCard";
import { useParams } from "react-router-dom";

const PageLayout = styled.div`
  display: flex;
  min-height: 100vh;
`;

const MainContent = styled.main`
  flex-grow: 1;
  padding: 20px;
  background-color: #f5f5f5;
`;

const PostGrid = styled.div`
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 20px;
`;

const Country = styled.div`
  color: #333;
  margin: 0;
  font-size: 24px;
  font-weight: bold;
`;

const FilterButtons = styled.div`
  margin-bottom: 20px;
`;

const FilterButton = styled.button`
  margin-right: 10px;
  padding: 5px 10px;
  background-color: ${(props) => (props.active ? "#FFD43B" : "#f8f9fa")};
  color: ${(props) => (props.active ? "white" : "black")};
  border: 1px solid #ffd43b;
  border-radius: 5px;
  cursor: pointer;

  &:hover {
    background-color: #ffd43b;
    color: white;
  }
`;

const CommunityPage = () => {
  const { category } = useParams();
  const text = category || "";
  const [posts, setPosts] = useState([]);
  const [sortBy, setSortBy] = useState("likes");
  const [selectedCountry, setSelectedCountry] = useState(null);

  const countryInfo = {
    canada: { korName: "", flag: "" },
    australia: { korName: "", flag: "" },
    newzealand: { korName: "", flag: "" },
    england: { korName: "", flag: "" },
    netherlands: { korName: "", flag: "" },
    germany: { korName: "", flag: "" },
  };

  const fetchPosts = useCallback(() => {
    //   API     .
    const fetchedPosts = [
      {
        id: 1,
        title: "   ",
        likes: 42,
        author: "123",
        date: "2023-07-15",
        preview: "     .",
        country: "canada",
      },
      {
        id: 2,
        title: "    ",
        likes: 38,
        author: "",
        date: "2023-07-20",
        preview: " ,   .",
        country: "australia",
      },
      {
        id: 3,
        title: "   ",
        likes: 55,
        author: "buff",
        date: "2023-08-05",
        preview: "     .",
        country: "germany",
      },
      {
        id: 4,
        title: "   ",
        likes: 60,
        author: "",
        date: "2023-08-10",
        preview: "     .",
        country: "england",
      },
      {
        id: 5,
        title: "   ",
        likes: 70,
        author: "",
        date: "2023-09-15",
        preview: "     .",
        country: "germany",
      },
    ];
    return fetchedPosts;
  }, []);

  useEffect(() => {
    const fetchedPosts = fetchPosts();
    setPosts(fetchedPosts);
  }, [fetchPosts]);

  const sortPosts = useCallback(
    (postsToSort) => {
      return [...postsToSort].sort((a, b) => {
        if (sortBy === "likes") {
          return b.likes - a.likes;
        } else {
          return new Date(b.date) - new Date(a.date);
        }
      });
    },
    [sortBy]
  );

  useEffect(() => {
    setPosts((prevPosts) => sortPosts(prevPosts));
  }, [sortBy, sortPosts]);

  const handleSortChange = (newSortBy) => {
    setSortBy(newSortBy);
  };

  const handleCountrySelect = (country) => {
    setSelectedCountry(country);
  };

  const filteredPosts = selectedCountry
    ? posts.filter((post) => post.country === selectedCountry)
    : posts;

  return (
    <>
      <Header />
      <PageLayout>
        <Sidebar text={text} />
        <MainContent>
          <Country></Country>
          <FilterButtons>
            {Object.entries(countryInfo).map(([code, info]) => (
              <FilterButton
                key={code}
                onClick={() => handleCountrySelect(code)}
                active={selectedCountry === code}
              >
                {info.flag} {info.korName}
              </FilterButton>
            ))}
            <FilterButton
              onClick={() => setSelectedCountry(null)}
              active={selectedCountry === null}
            >
              
            </FilterButton>
          </FilterButtons>
          <FilterButtons>
            <FilterButton
              onClick={() => handleSortChange("likes")}
              active={sortBy === "likes"}
            >
              
            </FilterButton>
            <FilterButton
              onClick={() => handleSortChange("date")}
              active={sortBy === "date"}
            >
              
            </FilterButton>
          </FilterButtons>
          <PostGrid>
            {filteredPosts.map((post) => (
              <PostCard
                key={post.id}
                title={post.title}
                likes={post.likes}
                author={post.author}
                date={post.date}
                preview={post.preview}
              />
            ))}
          </PostGrid>
        </MainContent>
      </PageLayout>
    </>
  );
};

export default CommunityPage;
