import React, { useState, useEffect, useCallback} from 'react';
import styled from 'styled-components';
import Header from '../components/Header';
import Sidebar from '../components/Sidebar';
import PostCard from '../components/PostCard';

const PageLayout = styled.div`
  display: flex;
  min-height: 100vh;
`;

const MainContent = styled.main`
  flex-grow: 1;
  padding: 20px;
  background-color: #f5f5f5;
`;

const PostGrid = styled.div`
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 20px;
`;

const Country = styled.div`
    color: #333;
    margin: 0;
    font-size: 24px;
    font-weight: bold;
`

const FilterButtons = styled.div`
margin-bottom: 20px;
`;

const FilterButton = styled.button`
margin-right: 10px;
padding: 5px 10px;
background-color: ${props => props.active ? '#FFD43B' : '#f8f9fa'};
color: ${props => props.active ? 'white' : 'black'};
border: 1px solid #FFD43B;
border-radius: 5px;
cursor: pointer;

&:hover {
  background-color: #FFD43B;
  color: white;
}
`;


const GroupBuying = () => {
    const [posts, setPosts] = useState([]);
    const [sortBy, setSortBy] = useState('likes');
  
    const fetchPosts = useCallback(() => {
        //     
        const fetchedPosts = [
            {
                id: 1,
                title: "eSim ",
                likes: 42,
                author: "",
                date: "2023-07-15",
                preview: "    eSim  . 15%    !",
                participantsCount: "/",
                targetCount: "7/20 "
            },
            {
                id: 2,
                title: "uSim ",
                likes: 38,
                author: "",
                date: "2023-07-20",
                preview: "    uSim 10% !",
                participantsCount: "/",
                targetCount: "7/24 "
            },
            {
                id: 3,
                title: "  ",
                likes: 55,
                author: "",
                date: "2023-07-23",
                preview: "    30% !   .",
                participantsCount: "",
                targetCount: "7/30 "
            },
            {
                id: 4,
                title: "  ",
                likes: 27,
                author: "",
                date: "2023-07-14",
                preview: "    20% !  !",
                participantsCount: "",
                targetCount: "7/19 "
            },
        ];
        return fetchedPosts;
    }, []);
  
    useEffect(() => {
      const fetchedPosts = fetchPosts();
      setPosts(fetchedPosts);
    }, [fetchPosts]);
  
    const sortPosts = useCallback((postsToSort) => {
      return [...postsToSort].sort((a, b) => {
        if (sortBy === 'likes') {
          return b.likes - a.likes;
        } else {
          return new Date(b.date) - new Date(a.date);
        }
      });
    }, [sortBy]);
  
    useEffect(() => {
      setPosts(prevPosts => sortPosts(prevPosts));
    }, [sortBy, sortPosts]);
  
    const handleSortChange = (newSortBy) => {
      setSortBy(newSortBy);
    };
  
    return (
        <>
            <Header />
            <PageLayout>
                <Sidebar />
                <MainContent>
                <Country></Country>
                    <FilterButtons>
                        <FilterButton 
                            onClick={() => handleSortChange('likes')}
                            active={sortBy === 'likes'}
                        >
                            
                        </FilterButton>
                        <FilterButton 
                            onClick={() => handleSortChange('date')}
                            active={sortBy === 'date'}
                        >
                            
                        </FilterButton>
                    </FilterButtons>
                    <PostGrid>
                        {posts.map(post => (
                            <PostCard 
                                key={post.id}
                                title={post.title}
                                likes={post.likes}
                                author={post.author}
                                date={post.date}
                                preview={post.preview}
                                continent={post.participantsCount}
                                country={post.targetCount}
                            />
                        ))}
                    </PostGrid>
                </MainContent>
            </PageLayout>
        </>
    );
  }
  
  export default GroupBuying;